import React from 'react'
import Button from 'components/Button'

import './style.scss'
import logo from 'assets/app-logo/logo-shadow.svg';

export default function HomeScreen({ history }) {

    function onClickDisabledButton() {
        navigator.vibrate(100);
    }

    return (
        <div className="home">
            <div className="pattern"></div>
            <img src={logo} alt="eishow logo" />
            {/* escuro = 008D0E, claro = 64C66E */}
            <Button background="#64C66E" textColor="white" onClick={() => history.push('/video')}>Ciências Humanas</Button>
            <Button background="#cccccc" textColor="white" onClick={onClickDisabledButton}>Ciências Naturais</Button>
            <Button background="#cccccc" textColor="white" onClick={onClickDisabledButton}>Matemática</Button>
            <Button background="#cccccc" textColor="white" onClick={onClickDisabledButton}>Língua Portuguesa</Button>

        </div>
    )
}
